/* stylelint-disable font-family-no-missing-generic-family-keyword */
@charset "UTF-8";

@font-face {
  font-family: "sliderm__fontello";
  src:
    url("data:application/octet-stream;base64,") format("woff"),
    url("data:application/octet-stream;base64,") format("truetype");
}

[class^="#{$sliderm-class}__icon-"]::before,
[class*=" #{$sliderm-class}__icon-"]::before {
  display: inline-block;
  width: 1em;
  margin-right: 0.2em;
  margin-left: 0.2em;
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  font-family: "sliderm__fontello";
  line-height: 1em;
  text-align: center;
  text-decoration: inherit;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.#{$sliderm-class} {
  &__icon-left--regular::before { content: "󾼁"; } /* '\fef01' */
  &__icon-right--regular::before { content: "󾼂"; } /* '\fef02' */
  &__icon-left--bold::before { content: "󾼃"; } /* '\fef03' */
  &__icon-right--bold::before { content: "󾼄"; } /* '\fef04' */
  &__icon-right--thin::before { content: "󾼅"; } /* '\fef05' */
  &__icon-left--thin::before { content: "󾼆"; } /* '\fef06' */
}
